
require('./orderList.less')
import EmptyView from '../../components/emptyView.vue'
import PayWay from '../../components/payWayPopup.vue'
import ShopCard from '../../components/shopCard.vue'
import GoodList from '../../components/GoodList.vue'
import EndingTip from '../../components/EndingTip.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
EmptyView,
Tab,
TabItem,
GoodList,
XButton,
EndingTip,
ViewBox,
ShopCard,
PayWay
},
filters: {
colorFilter(status) {
const statusMap = {
'订单结算': 'wancheng',
'订单付款': 'fukuan',
'订单失效': 'shixiao',
'已返利': 'fanli'
}
return statusMap[status]
},
priceFilter(price) {
if (localStorage.getItem("level") === 'vip'){
return Math.floor(price * 0.7 * 100)/100
}else if(localStorage.getItem("level") === 'wangzhe'){
return Math.floor(price * 0.5 * 100)/100
}else {
return Math.floor(price * 0.5 * 100)/100
}

}
},
data() {
return {
orderList: [],
list: [
{
tag: '	',
text: '全部'
},
{
tag: '已返利',
text: '已返利'
},
{
tag: '订单结算',
text: '订单结算'
},
{
tag: '订单付款',
text: '订单付款'
},
{
tag: '订单失效',
text: '订单失效'
}
],
index: 0
}
},
activated() {
this.initOrders()
},
computed: {
computedTag() {
return this.$route.query.tag
},
ComputedState() {

},
loading() {
return this.$store.getters.loading
}
},
methods: {
payBtnHandler(){

},
tabHandler(tag){
const that = this
this.$http.get('https://taoapi.04wu.com/my/orders?_format=json&jdadzoneid='+localStorage.getItem("jdadzoneid")+'&adzoneid='+localStorage.getItem("adzoneid")+'&field_order_status_value='+
tag)
.then(function(res){
that.orderList = res.data
})
.catch(function(err){
})
},
initOrders() {
if(this.$route.query.tag === undefined){
this.$route.query.tag = ''
}
const that = this
this.$http.get('https://taoapi.04wu.com/my/orders?_format=json&jdadzoneid='+localStorage.getItem("jdadzoneid")+'&adzoneid='+localStorage.getItem("adzoneid")+'&field_order_status_value='+
this.$route.query.tag)
.then(function(res){
that.orderList = res.data
})
.catch(function(err){
})
}
}
}



